.menu__item {
    --clip: polygon(0 0, 100% 0, 100% 0, 0 0);
    --transform: rotate(-90deg);
}

    .menu__item:hover {
        --clip: polygon(0 0, 100% 0, 100% 100%, 0 100%);
        --transform: rotate(0);
    }

.activarDesplegableMenu {
    --clip: polygon(0 0, 100% 0, 100% 100%, 0 100%) !important;
    --transform: rotate(0) !important;
}

.menu__nesting {
    list-style: none;
    transition: clip-path .3s;
    clip-path: var(--clip);
    position: absolute;
    left: 0;
    bottom: -2px;
    width: max-content;
    transform: translateY(100%);
    background-color: var(--Medium-primary);
    margin: 0px;
    padding: 0 0 5px 0;
}

    .menu__nesting li {
        padding: 5px 5px 2px 5px;
        transition: 0.3s;
        transition-property: border;
    }

        .menu__nesting li:hover {
            border-bottom: 1px solid #FE8538;
        }

            .menu__nesting li:hover a {
                color: #fff;
            }

    .menu__nesting a {
        font-size: 12px;
        font-weight: 400;
        color: #D0D0D0;
        padding: 5px;
        transition: 0.3s;
        transition-property: color;
        text-decoration: none;
    }

.menu__nestingCerrar {
    list-style: none;
    transition: clip-path .3s;
    clip-path: var(--clip);
    position: absolute;
    right: 0;
    bottom: 0;
    left: auto;
    width: 282px;
    transform: translateY(100%);
    background-color: var(--Medium-primary);
    padding-bottom: 5px;
    border-radius: 7px;
}

    .menu__nestingCerrar .subMenuCerrar {
        padding: 5px 0;
    }

    .menu__nestingCerrar li {
        padding: 5px 5px 2px 5px;
        transition: 0.3s;
        transition-property: border;
    }

        .menu__nestingCerrar li:hover {
            border-bottom: 1px solid #FE8538;
        }

            .menu__nestingCerrar li:hover a {
                color: #fff;
            }

    .menu__nestingCerrar a {
        font-size: 12px;
        font-weight: 400;
        color: #fff;
        padding: 5px;
        transition: 0.3s;
        transition-property: color;
    }



